header {
    /* 
        height: 100vh = 100% of the viewport height
        height: 100% = 100% of the parent's element height
     */
    height: 100vh;
    /* padding-top: 7rem; */

    /* нужно ли добавлять полосы прокрутки, обрезать итд  */
    overflow: hidden; 

    background-color:  var(--color-primary);
    color: var(--color-bg);
}

header a {
    color: var(--color-bg);
}


.header__container {
    text-align: center;
    height: 100%;
    position: relative;
    display: flex;
}

.greetings {
    margin: auto;
}

.greetings > h5 {
    font-size: 1rem;
}

.greetings > h1 {
    font-size: 4.2rem;
}

/* * ====== CTA ====== * */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.cta a.btn {
    color: var(--color-bg);
    border: 1px solid var(--color-bg);
}

.cta a.btn:hover {
    background-color: var(--color-bg);
    color: var(--color-primary);
}

.cta a.btn-primary {
    color: var(--color-primary);
    background-color: var(--color-bg);
}

/* * ====== SOCIALS ====== * */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px; 
    height: 2rem;
    background: var(--color-bg);
}

/* * ====== ME ====== * */
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: relative;
    top: 0;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}


/* * ====== SCROLL DOWN ====== * */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* * ========= MEDIA QUERIES (MEDIUM DEVICES / TABLETS) ========= * */
@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }

   
}


/* * ========= MEDIA QUERIES (SMALL DEVICES / PHONES) ========= * */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down {
        display: none;
    }

    .greetings > h1 {
        font-size: 3rem;
    }
}